.taskem {
	width: 99%;
	margin-left: 0.5%;
	height: 82px;
	border-radius: 4px;
	margin-top: 10px;
	margin-bottom: 4px;
}

.taskfocus {
	border: 1px solid #70c0e7;
	background-color: #e5f3fb;
}

.taskunfocus {
	border: 1px solid #e0e0e0;
	background-color: #f8f8f8;
}
.taskstate {
	width: 6px;
	height: 100%;
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
	float: left;
}
.taskinfo {
	width: 80%;
	height: 54px;
	float: left;
}
.taskinfo1 {
	width: 100%;
	float: left;
	height: 36px;
	line-height: 40px;
	padding-left: 12px;
}
.taskinfo2 {
	width: 100%;
	float: left;
	height: 18px;
}
.taskinfoem {
	width: 25%;
	float: left;
	line-height: 22px;
	padding-left: 12px;
	font-size: 12px;
	color: #aaaaaa;
}
.taskproc {
	height: 26px;
	width: 96%;
	float: left;
	margin-left: 12px;
}
.taskicons {
	float: left;
	margin-top: 24px;
}
.taskicon {
	float: left;
	margin-left: 12px;
}


