.titleLine {
	width: 100%;
	height: 52px;
	background-color: #3b4852;
	line-height:48px;
	padding-left: 32px;
	float: left;
}
.nav {
	background-color: #f0f0f0;
	border-right: solid 1px #cfcfcf;
	position: relative;
}
.logodiv {
	float: left;
}
.iconDiv {
	float: right;
	margin-top: 12px;
	margin-right: 10px;
}
.uinfopanel {
	width: 160px;
