	margin-top: 10px;
}
.setline {
	border-bottom: 0px solid #e0e0e0;
	width: 100%;
	height: 50px;
}
.fl {
	float: left;
}
.smallline {
	height: 30px;
	line-height: 30px;
	width: 100%;
}
.settitle {
	width: 200px;
}
.speed {
	margin-top: 14px;
	font-size: 14px;
	line-height: 14px;
	float: left;
	width: 100%;
	text-align: center;
	height: 18px;
}

.speeddiv {
	position: absolute;
	bottom: 60px;
}


