.rssitem {
	width: 100%;
	height: 70px;
	border-bottom: solid 1px #e3e3e3;
}
.rssInfo {
	height: 60px;
	float: left;
}
.cancelBtn {
	height: 60px;
	float: right;
}
.nameInfo {
	width: 100%;
	height: 40px;
	line-height: 40px;
	padding-left: 20px;
	font-size: 18px;
	float: left;
}
.rssinfo {
	width: 100%;
	height: 25px;
	line-height: 25px;
	padding-left: 25px;
	font-size: 14px;
	color: #999999;
	float: left;
}


