.plloading {
	width: 100%;
	height: 100%;
}
.plfont {
	font-size: 14px;
}
.plspin {
	position: absolute;
	top: 45%;
	-webkit-transform: translateY(-55%);
	    -ms-transform: translateY(-55%);
	        transform: translateY(-55%);
	left: 48%;
}
.pltoolbar {
	width: 100%;
	float: left;
	height: 48px;
	background-color: #f0f0f0;
	border-bottom: solid 1px #cfcfcf;
	padding-left: 10px;
}

.plsearch {
	float: left;
	margin-left: 20px;
	margin-top: 8px;
}



